/*------------------------------------*\
    #BTN
\*------------------------------------*/




.btn {
  @include prefix(border-radius, .2em);
  @extend .transition;
  -webkit-box-shadow: none;
  box-shadow: none;
  background: none;
  position: relative;
  border: 1px solid transparent;
  background: $blue-dark;
  &:hover {
    border-color: $blue-dark;
    background: transparent;
    color: $blue-dark;
  }
}
.btn--soft{
    @include prefix(border-radius,2em)
}
.btn--white,.btn--white--active:hover {
  background: transparent;
  border: $border;
}
.btn--white--active:hover{
  color: white;
}
.btn--white:hover,.btn--white--active {
  background: white;
  color: $blue-dark;
}

.btn-link {
  color: $gray-dark;
  text-decoration: underline;
  font-weight: bold;
}

.btn-link--white {
  color: white;
}

.btn+ .btn,.btn-link + .btn-link {
  margin-left: $inuit-base-spacing-unit;
}
.btn--alert {
  background: $red;
  color: white;
  border-color: transparent;
  &:hover {
    color: $red;
    border-color: $red;
    background: transparent;
  }
}

.btn--add{
  font-size: 16px;
  margin-left:$inuit-base-spacing-unit ;
}